<template>
  <!-- 主节点 -->
  <div class="masternode">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span class="card-title">{{ $t('masternode.title') }}</span>
      </div>
      <el-row type="flex" justify="center">
        <el-col :lg="12">
          <p>{{ $t('masternode.detail') }}</p>
        </el-col>
      </el-row>
    </el-card>

    <el-card class="referral-link box-card">
      <div slot="header" class="clearfix">
        <span class="card-title">{{ $t('masternode.referralLink') }}</span>
      </div>
      <el-row type="flex" justify="center">
        <el-col :lg="12">
          <p>
            {{ $t('masternode.yourReferralLink') }}
            <i class="el-icon-copy-document shj-copy" :data-clipboard-text="link + myEthAddress"></i> <!--添加复制功能-->
          </p>
          <p>
            <span class="link-address shj-copy" :data-clipboard-text="link + myEthAddress">{{ link }}{{ myEthAddress }}</span>
          </p>
          <p @show="display()">{{ $t('masternode.QRCode') }}</p>
          <div id="qrcode" ref="qrcode"></div>
          <p>{{ $t('masternode.severalSuggestions') }}</p>
          <ul>
            <li>{{ $t('masternode.suggestion1') }}</li>
            <li>{{ $t('masternode.suggestion2') }}</li>
            <li>{{ $t('masternode.suggestion3') }}</li>
            <li>{{ $t('masternode.suggestion4') }}</li>
          </ul>
        </el-col>
      </el-row>
    </el-card>

    <el-card class="referral-picture box-card" :body-style="{ padding: '0px'}">
      <div slot="header" class="clearfix">
        <span class="card-title">{{ $t('masternode.referralPicture') }}</span>
      </div>
      <el-row class="test-flex picture-style">
        <el-col :lg="6" :xs="24">
          <div class="container">
            <canvas id="myCanvas1" width="270px" height="480px"></canvas>
          </div>
          <a :href="resultSrc1" download="post1"><el-image id="post1" :src="resultSrc1" @click="instruction()"></el-image></a>
        </el-col>

        <el-col :lg="6" :xs="24" class="middle-pic">
          <div class="container">
            <canvas id="myCanvas2" width="270px" height="480px"></canvas>
          </div>
          <a :href="resultSrc2" download="post2"><el-image id="post2" :src="resultSrc2" @click="instruction()"></el-image></a>
        </el-col>

         <el-col :lg="6" :xs="24">
          <div class="container">
            <canvas id="myCanvas3" width="270px" height="480px"></canvas>
          </div>
          <a :href="resultSrc3" download="post3"><el-image id="post3" :src="resultSrc3" @click="instruction()"></el-image></a>
        </el-col>
      </el-row>
    </el-card>
    <div id="popWindow">复制成功</div>
  </div>
</template>

<script>
import QRCode from 'qrcodejs2'
import { setTimeout } from 'timers'
import {urlFilter} from '../Mixin/urlFilter'
import hrefSlice from '../utils/hrefSlice'
import ClipboardJS from 'clipboard'
import {metaMaskInfo} from '../Mixin/metaMaskInfo'
import store from '../store/index'
let flag = 0
let screenWidth = screen.width
export default {
  name: 'MasterNode',
  mixins: [urlFilter, metaMaskInfo],
  data () {
    return {
      flag: 0,
      state1: 0,
      state2: 0,
      Qrcode: {
        qrcode1: '',
        qrcode2: '',
        qrcode3: '',
        qrcode4: ''
      },
      resultSrc1: '',
      resultSrc2: '',
      resultSrc3: '',
      link: hrefSlice(),
      src1: 'https://i.loli.net/2019/07/30/5d3fc5f70271160141.jpg',
      src2: 'https://i.loli.net/2019/07/30/5d3fc5f70470b78944.jpg',
      src3: 'https://i.loli.net/2019/07/30/5d3fc5f704d8c75717.jpg'
    }
  },
  computed: {
    myEthAddress () {
      return this.$store.state.metamask.currentAccount
    },
    commitMark: function () {
      return this.$store.state.metamask.commitMark
    }
  },
  watch: {
    commitMark () {
      const status = this.$store.state.metamask.status
      if (status !== 1) {}
    },
    myEthAddress () {
      if (sessionStorage.getItem('user') === this.$store.state.metamask.currentAccount || this.$store.state.metamask.currentAccount === '') { // 刷新
        this.CodeRefresh()
        this.getPosterSrc()
      } else { // 更换账户
        this.CodeRefreshAndGenerate()
        this.generatePosters()
      }
    }
  },
  methods: {
    // 推荐人二维码
    qrcode () {
      if (sessionStorage.getItem('user') !== null && sessionStorage.getItem('user') === store.state.metamask.currentAccount) {
        this.Qrcode.qrcode1 = new QRCode('qrcode', {
          width: 200, // 设置宽度，单位像素
          height: 200, // 设置高度，单位像素
          text: this.link + sessionStorage.getItem('user') // 设置二维码内容或跳转地址
        })
        document.getElementById('qrcode').children[1].setAttribute('style', 'padding:2px;background:white;')
      } else {
        this.Qrcode.qrcode1 = new QRCode('qrcode', {
          width: 200, // 设置宽度，单位像素
          height: 200, // 设置高度，单位像素
          text: this.link + store.state.metamask.currentAccount // 设置二维码内容或跳转地址
        })
        document.getElementById('qrcode').children[1].setAttribute('style', 'padding:2px;background:white;')
      }
    },
    generateFirstPost () { // 1.生成二维码  2.canvas画图  3.转成图片
      let img = new Image()
      let canvas = document.getElementById('myCanvas1')
      let ctx = canvas.getContext('2d')

      img.src = this.src1
      img.setAttribute('crossOrigin', 'Anonymous')
      img.onload = function () {
        let code = document.getElementById('qrcode').children[0]
        if (screenWidth < 768) {
          ctx.drawImage(img, 0, 0, screenWidth, screenWidth * 16 / 9)
          ctx.drawImage(code, (screenWidth - screenWidth / 3) / 2, screenWidth * 16 / 9 * 0.75, screenWidth / 3 + 4, screenWidth / 3 + 4) // 95 355
          ctx.strokeStyle = '#FFF'
          ctx.lineWidth = 2
          ctx.strokeRect((screenWidth - screenWidth / 3 - 4) / 2, screenWidth * 16 / 9 * 0.75 - 2, screenWidth / 3 + 8, screenWidth / 3 + 8)
        } else {
          ctx.drawImage(img, 0, 0, 270, 480)
          ctx.drawImage(code, 85, 355, 100, 100) // 95 355
          ctx.strokeStyle = '#FFF'
          ctx.lineWidth = 2
          ctx.strokeRect(83, 353, 104, 104)
        }
      }
      setTimeout(() => {
        let myCanvas = document.getElementById('myCanvas1') // 画图后用另一个变量接收画布对象
        let image = myCanvas.toDataURL('image/png')
        this.resultSrc1 = image
        sessionStorage.setItem('user', store.state.metamask.currentAccount)
        sessionStorage.setItem('postAddr1', image)
      }, 200)
    },
    generateSecondPost () { // 1.生成二维码  2.canvas画图  3.转成图片
      let img = new Image()
      let canvas = document.getElementById('myCanvas2')
      let ctx = canvas.getContext('2d')

      img.src = this.src2
      img.setAttribute('crossOrigin', 'Anonymous')
      img.onload = function () {
        let code = document.getElementById('qrcode').children[0]
        if (screenWidth < 768) {
          ctx.drawImage(img, 0, 0, screenWidth, screenWidth * 16 / 9)
          ctx.drawImage(code, (screenWidth - screenWidth / 3) / 2, screenWidth * 16 / 9 * 0.722, screenWidth / 3 + 4, screenWidth / 3 + 4) // 95 355
          ctx.strokeStyle = '#FFF'
          ctx.lineWidth = 2
          ctx.strokeRect((screenWidth - screenWidth / 3 - 4) / 2, screenWidth * 16 / 9 * 0.722 - 2, screenWidth / 3 + 8, screenWidth / 3 + 8)
        } else {
          ctx.drawImage(img, 0, 0, 270, 480)
          ctx.drawImage(code, 85, 340, 100, 100)
          ctx.strokeStyle = '#FFF'
          ctx.lineWidth = 2
          ctx.strokeRect(83, 338, 104, 104)
        }
      }

      setTimeout(() => {
        let mycanvas = document.getElementById('myCanvas2') // 画图后用另一个变量接收画布对象
        let image = mycanvas.toDataURL('image/png')
        this.resultSrc2 = image
        sessionStorage.setItem('postAddr2', image)
      }, 200)
    },
    generateThirdPost () { // 1.生成二维码  2.canvas画图  3.转成图片
      let img = new Image()
      let canvas = document.getElementById('myCanvas3')
      let ctx = canvas.getContext('2d')

      img.src = this.src3
      img.setAttribute('crossOrigin', 'Anonymous')
      img.onload = function () {
        let code = document.getElementById('qrcode').children[0]
        if (screenWidth < 768) {
          ctx.drawImage(img, 0, 0, screenWidth, screenWidth * 16 / 9)
          ctx.drawImage(code, (screenWidth - screenWidth / 3) / 2, screenWidth * 16 / 9 * 0.76, screenWidth / 3 + 4, screenWidth / 3 + 4) // 95 355
          ctx.strokeStyle = '#FFF'
          ctx.lineWidth = 2
          ctx.strokeRect((screenWidth - screenWidth / 3 - 4) / 2, screenWidth * 16 / 9 * 0.76 - 2, screenWidth / 3 + 8, screenWidth / 3 + 8)
        } else {
          ctx.drawImage(img, 0, 0, 270, 480)
          ctx.drawImage(code, 85, 360, 100, 100)
          ctx.strokeStyle = '#FFF'
          ctx.lineWidth = 2
          ctx.strokeRect(83, 358, 104, 104)
        }
      }

      setTimeout(() => {
        let mycanvas = document.getElementById('myCanvas3') // 画图后用另一个变量接收画布对象
        let image = mycanvas.toDataURL('image/png')
        this.resultSrc3 = image
        sessionStorage.setItem('postAddr3', image)
      }, 200)
    },
    copy () {
      let str = this.link + store.state.metamask.currentAccount
      let oInput = document.createElement('input')
      oInput.value = str
      document.body.appendChild(oInput)
      oInput.select()
      document.execCommand('Copy')
      oInput.style.display = 'none'
      document.body.removeChild(oInput)
      let popWindow = document.getElementById('popWindow')
      popWindow.setAttribute('style', 'opacity:1;')
      setTimeout(function () {
        popWindow.setAttribute('style', 'opacity:0;')
      }, 1000)
    },
    instruction () {
      this.$notify({
        title: 'notification',
        message: 'The poster is being downloaded.',
        type: 'info',
        position: 'bottom-left'
      })
    },
    CodeRefresh () {
      // 清除原先全部二维码并重新生成
      let code = document.getElementById('qrcode')
      for (let i = 0; i < code.childNodes.length; i++) {
        code.removeChild(code.childNodes[i])
      }
      this.qrcode()
    },
    CodeRefreshAndGenerate () {
      // 清除原先全部二维码并重新生成
      let code = document.getElementById('qrcode')
      for (let i = 0; i < code.childNodes.length; i++) {
        code.removeChild(code.childNodes[i])
      }
      this.qrcode()
      for (let i = 0; i < code.childNodes.length - 1; i++) {
        code.removeChild(code.childNodes[i])
      }
    },
    getPosterSrc () {
      let addr1 = sessionStorage.getItem('postAddr1')
      let addr2 = sessionStorage.getItem('postAddr2')
      let addr3 = sessionStorage.getItem('postAddr3')

      this.resultSrc1 = addr1
      this.resultSrc2 = addr2
      this.resultSrc3 = addr3
    },
    generatePosters () {
      this.generateFirstPost()
      this.generateSecondPost()
      this.generateThirdPost()
      if (this.flag === 1) {
        location.reload()
      }
      this.flag = 1
    }
  },
  beforeCreate () {
    flag = 0
  },
  created () {
    if (sessionStorage.getItem('user') !== null && sessionStorage.getItem('user') === store.state.metamask.currentAccount) {
      this.getPosterSrc()

      // todo 可以使用vuex commitmark属性配合status属性判断，是否需要提示用户登陆钱包，生成推荐链接
      const commitMark = this.$store.state.metamask.commitMark
      const status = this.$store.state.metamask.status
      if (commitMark !== 0 && status !== 1) {
        this.$notify({
          title: this.metaInfo.warning,
          message: this.metaInfo.createAddress,
          type: 'warning'
        })
      }
    }
  },
  mounted () {
    if (screenWidth < 768) { // 使画布适配屏幕宽度
      let myCanvas1 = document.querySelector('#myCanvas1')
      let myCanvas2 = document.querySelector('#myCanvas2')
      let myCanvas3 = document.querySelector('#myCanvas3')
      myCanvas1.setAttribute('width', screenWidth + 'px')
      myCanvas1.setAttribute('height', (screenWidth * 16 / 9) + 'px')

      myCanvas2.setAttribute('width', screenWidth + 'px')
      myCanvas2.setAttribute('height', (screenWidth * 16 / 9) + 'px')

      myCanvas3.setAttribute('width', screenWidth + 'px')
      myCanvas3.setAttribute('height', (screenWidth * 16 / 9) + 'px')
    }

    // 复制功能
    const self = this
    const clipboard = new ClipboardJS('.shj-copy')
    clipboard.on('success', function (e) {
      self.$notify({
        title: self.metaInfo.success,
        message: self.metaInfo.copySuccess,
        type: 'success',
        position: 'top-left'
      })
      e.clearSelection()
    })
    clipboard.on('error', function (e) {
      self.$notify({
        title: self.metaInfo.error,
        message: self.metaInfo.copyFail,
        type: 'warning',
        position: 'top-left'
      })
    })
    this.$nextTick(() => { // 这里mounted挂载后执行生成二维码函数会触发视图更新,使得挂载函数二度触发,渲染dom是异步操作
      if (flag === 0) {
        this.state1 = 1
        flag = 1
        if ((sessionStorage.getItem('user') !== null && sessionStorage.getItem('user') === this.myEthAddress)) {
          this.qrcode()
        } else {
          if (sessionStorage.getItem('state') === null) { // 第一次进入页面
            this.qrcode() // 赋值同步，渲染异步
            this.generatePosters()
            sessionStorage.setItem('state', 1)
          } else if (sessionStorage.getItem('user') !== this.myEthAddress && this.myEthAddress !== '') { // 用户在其他页面下切换用户然后进入masterNode
            // 清除原先二维码重新生成
            this.CodeRefresh()
            this.generatePosters()
          }
        }
      }
    })
  }
}
</script>

<style lang="scss" scoped>
  @import "../assets/variables.scss";
  .masternode {
    font-size: 16px;
    .referral-link {
      .link-address {
        background-color: $border;
        border-radius: 4px;
        color: #00897b;
        font-weight: 700;
        text-decoration: none;
        word-break: break-all;
      }
    }
    .referral-picture {
      height: 100%;
    }
  }

.codeContainer{
  min-width: 400px;
  width: 70%;
  margin: 0 auto;
  height: auto;
}

  .container{
    display: none;
  }
  #popWindow{
    width: 100px;
    height: 30px;
    color: gray;
    position: absolute;
    bottom: 50px;
    left: 50%;
    margin-left: -50px;
    opacity: 0;
    transition: all 1s;
  }
  #post1{
    width: 500px;height: auto;
  }
</style>
<style scoped>
.el-card {
  margin-top: 1.5rem;
  background-color: #252323;
  border: 0px solid #252323;
  color: #dac28f;
}
.box-card >>> .el-card__header {
  padding: 8px 20px;
  background: linear-gradient(to bottom, #efdbca, #fdd650);
  font-size: 16px;
  border: 0px solid #f9d76c;
}
</style>



